class Notifications {
  el = document.querySelector('.notification')

  calls = []

  texts = []

  counter = 0

  getText = (element) => {
    this.texts.push(element.textContent)
    element.textContent = ''
  }

  setText = (element, delay) => {

    if (!this.isRunning) {

      this.isRunning = true

      const string = this.texts[0]
      this.texts.shift()

      for (const pos in string) {
        setTimeout(() => {
          element.textContent += string[pos]
          if (pos == string.length - 1) {
            this.isRunning = false
            this.counter++
            if (this.counter === 2) {
              setTimeout(main.modal.show, 200)
            }
            if (this.calls.length) {
              this.setText(this.calls[0].element, this.calls[0].delay)
              this.calls.shift()
            }
          }
        }, delay * (+pos + 1))
      }
    } else {
      this.calls.push({ element, delay })
    }
  }

  show = () => {
    this.el.classList.remove('hide')
  }
}

class ProjectsModal {

  ntBtn = document.querySelector('.notification')
  body = document.querySelector('body')

  modal = document.querySelector('.modal')
  modalBg = document.querySelector('.modal__bg')
  closeBtn = document.querySelector('.modal__close')

  show = () => {
    this.body.classList.remove('out')
    this.body.classList.add('modal-active')
  }

  hide = () => {
    this.body.classList.add('out')
  }

  init() {
    this.ntBtn.addEventListener('click', this.show)
    this.closeBtn.addEventListener('click', this.hide)
    this.modalBg.addEventListener('click', this.hide)
  }
}

class ImgCanvas {
  constructor(image, speed, ctx, counter) {
    this.ctx = ctx
    this.image = image
    // this.w = Math.floor(Math.random() * (image.width - 20 - 30) + 30)
    this.w = Math.floor(Math.random() * (window.innerWidth *0.1 - 20 - 30) + 30)
    this.h = this.w * (image.height / image.width)
    this.posX = Math.floor(Math.random() * (window.innerWidth - this.w))
    this.posY = -image.height
    this.speed = speed
    this.id = counter
  }

  render = () => {
    this.posY += this.speed
    this.ctx.drawImage(this.image, this.posX, this.posY, this.w, this.h)
    if (this.posY > window.innerHeight) {
      main.canvas.removeInstance()
      return
    }
    requestAnimationFrame(this.render)
  }

  init = () => {
    requestAnimationFrame(this.render)
  }

}

class BgCanvas {

  canvas = document.querySelector('.home__bg')
  ctx = this.canvas.getContext('2d')


  files = ['block1', 'block2', 'block3']
  images = []
  instances = []

  promises = []

  loadImg = (file) => {
    return new Promise((resolve) => {
      const img = new Image()
      img.addEventListener('load', () => {
        resolve(img)
        this.images.push(img)
      })
      img.src = `./img/canvas/${file}.png`
    })
  }

  counter = 0

  drawImg = () => {
    this.counter++
    this.images.forEach((image) => {
      this.instances.push(
        new ImgCanvas(image, Math.random() * 10 + 5, this.ctx, this.counter)
      )
      this.instances[this.instances.length - 1].init()
    })
    setTimeout(this.drawImg, 200)
  }

  removeInstance = () => {
    this.instances.shift()
  }

  clear = () => {
    this.ctx.clearRect(0, 0, window.innerWidth, window.innerHeight)
    requestAnimationFrame(this.clear)
  }

  init = () => {
    this.canvas.setAttribute('width', window.innerWidth)
    this.canvas.setAttribute('height', window.innerHeight)
    this.files.forEach((i) => {
      this.promises.push(this.loadImg(i))
    })
    Promise.all(this.promises).then(() => {
      requestAnimationFrame(this.clear)
      this.drawImg()
    })
  }

}

class Main {

  notif = new Notifications()
  modal = new ProjectsModal()
  canvas = new BgCanvas()

  notifyEl = document.querySelector('.notification')
  notifyTexts = document.querySelectorAll('.notification .notification__item')


  init() {
    this.notifyEl.classList.remove('no-js')
    this.notifyTexts.forEach(item => this.notif.getText(item))
    this.modal.init()
    setTimeout(() => {
      this.notif.show()
      this.notifyTexts.forEach(item => this.notif.setText(item, Math.random() * 20 + 30))
    }, 1000)
    this.canvas.init()
  }
}

const main = new Main()

main.init()